.additionally {
    padding: 20px;
        background: white;
        z-index: 5;
    width: 80%;
    box-shadow: rgb(153, 153, 153) 0px 0px 5px 3px;
    border-radius: 15px;
    margin-bottom: 50px;
  }
  
  .additionally_roof {
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    overflow: hidden;
    gap: 20px;
  }
  
  .additionally_roof.reverse {
    flex-direction: row-reverse;
  }
  
  .additionally-roof_special-container {
    flex: 1;
    padding: 20px;
    display: flex;
        flex-direction: column;
        align-items: center;
  }
  
  .additionally_roof-title {
    font-size: 24px;
    color: var(--color-green-elements);
    margin-bottom: 15px;
  }
  
  .additionally-roof_special-text {
    font-size: 18px;
    color: #000;
    margin-bottom: 20px;
  }
  
  .additionally_roof_bot-div h3 {
    /* font-size: 20px;
    color: var(--color-salat-elements);
    margin-bottom: 10px; */
  }
  
  .special-text_list {
    padding-left: 20px;
  }
  
  .special-text_list li {
    margin-bottom: 10px;
    font-size: 16px;
  }
  
  .additionally-roof_special-img {
    flex: 1;
    width: 50%;
    object-fit: cover;
border-radius: 15px;
  }
  